import * as Steps from './TourGuideSteps';

export const GettingStarted = ({ index, currentId }, defaultProps) => {
	let currentProp = Steps.GettingStarted[index];
	if (index === 4) {
		currentProp.nextToIndex = localStorage.getItem('f-rules') ? 5 : 100;
	}
	if (index === 100) {
		currentProp = {
			index: 100,
			placement: 'left',
			message: `You can always check the rules in here.`,
			find() {
				return document.querySelector('.scroll-nav a[href="#rules"]');
			},
			nextToIndex: 5,
			backToIndex: 4,
			navigation: null,
		};
	}
	return {
		index,
		...defaultProps,
		navigation: Steps.GettingStarted.length,
		...currentProp,
	};
};

