export const CustomizeRules = ({ index, currentId }, defaultProps) => {
	const steps = Steps.CustomizeRules[currentId] || [];
	let currentProp = steps[index];
	return {
		index,
		...defaultProps,
		navigation: steps.length,
		...currentProp,
		id: currentId in Steps.CustomizeRules ? currentId : defaultProps.id,
	};
};

