import React from 'react';
import { Section, Table } from 'Templates'; 

export const Rules = () => (
	<Section title='Rules'>
		<div className='n-flat of-y-auto br-1r'>
			<table className='table-api n-table-bordered n-text-color'>
				<thead>
					<tr>
						<th>Name</th>
						<th className='width-200'>How to use</th>
						<th>Description</th>
						<th className='width-200'>Message</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td> required </td>
						<td> required </td>
						<td> it will require the form field to be filled</td>
						<td> The :attribute field is required </td>
					</tr>
					<tr>
						<td> email </td>
						<td> email </td>
						<td> it will validate if the field contain a valid e-mail</td>
						<td> The :attribute field must be valid email</td>
					</tr>
					<tr>
						<td> min </td>
						<td> min:{'<number>'} </td>
						<td>
							it will validate the minumum character, number, checkbox is
							checked, select(multiple) is selected, file(multiple) is selected.
							<span className='h-word'>e.g. min:10</span>{' '}
						</td>
						<td>
							The :attribute field must be atleast :min (character, items,
							files){' '}
						</td>
					</tr>
					<tr>
						<td> max </td>
						<td> max:{'<number>'} </td>
						<td>
							it will validate the maximum character, number, checkbox is
							checked, select(multiple) is selected, file(multiple) is selected.
							<span className='h-word'>e.g. max:20</span>
						</td>
						<td>
							The :attribute field may not be greater than :max (character,
							items, files)
						</td>
					</tr>
					<tr>
						<td> mimes </td>
						<td> mimes:{'<files mimes>'} </td>
						<td>
							it will validate the specific mimes of the files which are
							allowed.
							<span className='h-word'>e.g. mimes:jpg,pdf,rar</span>
						</td>
						<td> The :attribute only allows :mimes</td>
					</tr>
					<tr>
						<td> alpha </td>
						<td> alpha </td>
						<td>
							{' '}
							it will validate if the field value is only contain letter{' '}
						</td>
						<td> The :attribute may only contain letters</td>
					</tr>
					<tr>
						<td> alpha_space</td>
						<td> alpha_space </td>
						<td>
							{' '}
							it will validate if the field only contain letters with spaces
						</td>
						<td> The :attribute must contain alphabet with spaces </td>
					</tr>
					<tr>
						<td> alpha_num </td>
						<td> alpha_num </td>
						<td> it will validate if the field contain letters with numbers</td>
						<td>The :attribute may only contain letters and numbers.</td>
					</tr>
					<tr>
						<td> alpha_slug </td>
						<td> alpha_slug </td>
						<td>
							it will validate if the field contain letters, numbers, and
							underscore
						</td>
						<td>
							The :attribute may only contain letters, numbers, and underscore.
						</td>
					</tr>
					<tr>
						<td> alpha_dash </td>
						<td> alpha_dash </td>
						<td>
							it will validate if the field contain letters with numbers and
							dashes
						</td>
						<td>
							{' '}
							The :attribute may only contain letters, numbers, and dashes.
						</td>
					</tr>
					<tr>
						<td> url </td>
						<td> url </td>
						<td> it will validate if the field contain valid url</td>
						<td> The :attribute must be a valid url. </td>
					</tr>
					<tr>
						<td> max_size </td>
						<td> max_size:{'<number>'} </td>
						<td>
							it will validate if the field contain a maximum file size and the
							size must calculate in kilobytes.
							<span className='h-word'>e.g. max_size:5000</span>
						</td>
						<td> The :attribute may not be greater :max_size kilobytes.</td>
					</tr>
					<tr>
						<td> min_size </td>
						<td> min_size:{'<number>'} </td>
						<td>
							it will validate if the field contain a minimum file size and the
							size must calculate in kilobytes.
							<span className='h-word'>e.g. min_size:1000</span>
						</td>
						<td>The :attribute must be atleast :min_size kilobytes.</td>
					</tr>
					<tr>
						<td> required_if</td>
						<td>
							required_if:{'<field_id>'}={'<expected_value>'}
						</td>
						<td>
							it will require the field, if the target field matches the
							expected value. you can use exact value or regular expression like
							<span className='h-word'>required_if:bio=.+</span>.{' '}
							<span className='h-word'>.+</span> means has any value.
							<span className='h-word'>e.g. required_if:country=AU</span> since
							most of the time field ids are not the same as the labels and same
							with the values label. that is why you can use Aliasing(@)
							<span className='h-word'>
								e.g. required_if:country@Country=AU@Australia
							</span>
						</td>
						<td>
							The :attribute field is required when :required_if is
							:value.
						</td>
					</tr>
					<tr>
						<td> same </td>
						<td> same:{'<field_id>'} </td>
						<td>
							it will validate the field until the target field contain the same
							value.<span className='h-word'>e.g. same:pass</span> since most of
							the time field ids are not the same as the labels. you can use
							Aliasing(@)<span className='h-word'>e.g. same:pass@Password</span>
						</td>
						<td> The :attribute and :same must match. </td>
					</tr>
				</tbody>
			</table>
		</div>
	</Section>
);

