const ruleMessage = {
	name: 'message',
	type: 'string',
	default: '-',
	description:
		'it is the message template will be displayed when your rules is invalid.',
	data: [
		{
			name: '`:attribute`',
			type: 'interpulation',
			default: '-',
			description:
				'if it is part of your message, it will be replace by the label you assigned in label schema.',
		},
		{
			name: <pre>`:[ruleName]`</pre>,
			type: 'interpulation',
			default: '-',
			description:
				'if it is part of your message, it will be replace by the parameter you put on the rules like `min:5` then `:min` will be replaced by `5`.',
		},
		{
			name: '`:value`',
			type: 'interpulation',
			default: '-',
			description:
				'if it is part of your message, it will be replace by the value of your parameter like required_if:favorite=apple, then `:value` will be replaced by `apple`.',
		},
	],
};

